import React from "react";
import { Switch, Route } from "react-router-dom";
import { useMediaQuery } from "react-responsive";
import Announcement from "./components/announcement/announcement.component";
import Header from "./components/header/header.component";
import HeaderMobile from "./components/header-mobile/header-mobile.component";
import HomePage from "./pages/homepage/homepage.component";
import LocationPage from "./pages/location/location.component";
import MenuPage from "./pages/menu/menu.component";
import AboutPage from "./pages/about/about.component";
import ContactPage from "./pages/contact/contact.component";
import OrderPage from "./pages/order/order.component";
import PageNotFound from "./pages/404/404.component";
import Modal from "./components/subscribe-modal/subscribe-modal.component";
import Footer from "./components/footer-mobile/footer-mobile.component";

import "./App.scss";

// Media queries using hook
const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({ maxWidth: 768 });
  return isMobile ? children : null;
};
const Default = ({ children }) => {
  const isNotMobile = useMediaQuery({ minWidth: 769 });
  return isNotMobile ? children : null;
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
      // When showing model, hide overflow
      bodyOverflow: false
    };
    this.handleShowModal = this.handleShowModal.bind(this);
    this.handleCloseModal = this.handleCloseModal.bind(this);
  }

  // Hide overflow when modal is open by adding class to body
  componentDidUpdate(prevProps, prevState) {
    const { bodyOverflow } = this.state;
    if (prevProps.bodyOverflow !== bodyOverflow) {
      document.body.classList.toggle("modal-open");
    }
  }

  handleShowModal() {
    this.setState({
      showModal: true,
      bodyOverflow: true
    });
  }

  handleCloseModal() {
    this.setState({
      showModal: false,
      bodyOverflow: false
    });
  }

  render() {
    const PUBLIC_URL = process.env.PUBLIC_URL;

    return (
      <div className="App">
        {this.state.showModal ? (
          <Modal
            show={this.state.showModal}
            handleCloseModal={this.handleCloseModal}
          />
        ) : null}
        <div className="stick-top">
          {/* TODO: Add it only when announcement is valid */}
          <Announcement />

          <Mobile>
            <HeaderMobile handleShowModal={this.handleShowModal} />
          </Mobile>
        </div>

        <div className="main-container">
          {/* Desktop header */}
          {/* <MediaQuery minDeviceWidth={741}> */}
          <Default>
            <Header handleShowModal={this.handleShowModal} />
          </Default>
          {/* </MediaQuery> */}

          <Switch>
            {/* <Route exact path="/" component={HomePage} />
            <Route exact path="/location" component={LocationPage} />
            <Route exact path="/menu" component={MenuPage} />
            <Route exact path="/about" component={AboutPage} />
            <Route exact path="/order" component={OrderPage} />
            <Route exact path="/contact" component={ContactPage} />
            <Route>{PageNotFound}</Route> */}

            {/* Use relative path. i.e. name.io/restaurant-app/main */}
            <Route exact path={PUBLIC_URL + "/"} component={HomePage} />
            <Route
              exact
              path={PUBLIC_URL + "/location"}
              component={LocationPage}
            />
            <Route exact path={PUBLIC_URL + "/menu"} component={MenuPage} />
            <Route exact path={PUBLIC_URL + "/about"} component={AboutPage} />
            <Route exact path={PUBLIC_URL + "/order"} component={OrderPage} />
            <Route
              exact
              path={PUBLIC_URL + "/contact"}
              component={ContactPage}
            />
            <Route>{PageNotFound}</Route>
          </Switch>
        </div>
        <Mobile>
          <Footer />
        </Mobile>
      </div>
    );
  }
}

export default App;
