import React, { Component } from "react";
import { NavLink } from "react-router-dom/";
import logo from "../../assets/images/logo-square.png"; // Tell webpack this js uses this img
import classnames from "classnames";
import HamburgerMenu from "../hamburger-menu/hamburger-menu.component";

import "./header-mobile.styles.scss";

class HeaderMobile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      //   prevScrollPos: window.pageYOffset,
      visible: true
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = () => {
    // const { prevScrollPos } = this.state;
    const curScrollPos = window.pageYOffset;
    // console.log(curScrollPos);///
    // Use this to make it visible every time scrolling up from anywhere
    // const visible = prevScrollPos > curScrollPos;
    // Visible only at the top
    const visible = curScrollPos < 7;

    this.setState({
      prevScrollPos: curScrollPos,
      visible
    });
  };

  render() {
    // console.log(this.props); ///
    // const { handleHideMenu, ...rest } = this.props;
    return (
      <div className="header-mobile">
        <div className="header-mobile-logo-hamburger">
          <NavLink exact={true} to="/" className="logo-container">
            <div className="header-logo">
              <img src={logo} alt="logo" />
            </div>
          </NavLink>
          <div className="header-hamburger">
            {/* TODO: Fix prop drilling */}
            <HamburgerMenu
              handleShowModal={this.props.handleShowModal}
              scrolled={!this.state.visible}
            />
          </div>
        </div>
        <div
          className={classnames("header-mobile-address", {
            "header-mobile-address--hidden": !this.state.visible
          })}
        >
          <a
            className="location-text-address"
            href="https://goo.gl/maps/TyYEfbEYy77Qbifm7"
            target="_blank"
            rel="noopener noreferrer"
          >
            <span>461 W Gilman St, Madison, WI 53703</span>
          </a>
        </div>
      </div>
    );
  }
}

export default HeaderMobile;
