import React from "react";
import FormInput from "../form-input/form-input.component";
import "./contact-form.styles.scss";

class ContactForm extends React.Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      phoneNumber: "",
      message: ""
    };
  }

  handleSubmit = async event => {
    event.preventDefault();
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  render() {
    const { name, email, phoneNumber, message } = this.state;
    return (
      <div className="contact">
        <h1 className="center leading">We'd love to hear from you</h1>

        <p className="center text">
          Send us message, and we'll get back to you as soon as possible
        </p>
        {/* TODO: add handleSubmit onSubmit */}
        <form className="contact-form">
          <FormInput
            type="text"
            name="name"
            value={name}
            onChange={this.handleChange}
            label="Full Name"
            required
          />
          <FormInput
            type="email"
            name="email"
            value={email}
            onChange={this.handleChange}
            label="Email"
            required
          />
          {/* TODO: Use validator */}
          <FormInput
            type="tel"
            maxLength="12"
            name="phoneNumber"
            value={phoneNumber}
            onChange={this.handleChange}
            label="Phone Number"
            required
          />

          <FormInput
            textArea
            rows="5"
            cols="50"
            name="message"
            value={message}
            onChange={this.handleChange}
            label="Your Message"
            required
          />
          <button className="btn btn-custom">SUBMIT</button>
        </form>
      </div>
    );
  }
}

export default ContactForm;
