import React from "react";

import mapboxgl from "mapbox-gl";

import "./map.styles.scss";

// TODO: Put in back end
mapboxgl.accessToken = process.env.REACT_APP_MAPBOX_TOKEN;

const loc = [-89.39522, 43.073419];

class Map extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lng: -89.3952,
      lat: 43.0734,
      //   center: loc,
      zoom: 16.07
    };
  }

  componentDidMount() {
    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: "mapbox://styles/mapbox/streets-v11",
      center: [this.state.lng, this.state.lat],
      zoom: this.state.zoom
    });

    // Add marker
    new mapboxgl.Marker().setLngLat(loc).addTo(map);

    // Stores new latitude, longitude, and zoom
    // that you get when user interacts with the map
    // When using, need to clean up to prevent memory leak
    // map.on("move", () => {
    //   this.setState({
    //     lng: map.getCenter().lng.toFixed(4),
    //     lat: map.getCenter().lat.toFixed(4),
    //     zoom: map.getZoom().toFixed(2)
    //   });
    // });
  }

  render() {
    return (
      <div>
        {/* Side bar to show longitude, latitude, zoom. Uncomment to edit lng, lat, zoon */}
        {/* <div className="sidebarStyle">
          <div>
            Longitude: {this.state.lng} | Latitude: {this.state.lat} | Zoom:{" "}
            {this.state.zoom}
          </div>
        </div> */}
        <div ref={el => (this.mapContainer = el)} className="mapContainer">
          <a
            href="https://goo.gl/maps/TyYEfbEYy77Qbifm7"
            className="btn get-direction btn-custom"
            target="_blank"
            rel="noopener noreferrer"
          >
            get directions
          </a>
        </div>
      </div>
    );
  }
}

export default Map;
