import React, { Component } from "react";
import PropTypes from "prop-types";
import MenuTab from "../menu-tab/menu-tab.component";
import "./menu-tabs.styles.scss";

class MenuTabs extends Component {
  static propTypes = {
    children: PropTypes.instanceOf(Array).isRequired
  };

  constructor(props) {
    super(props);
    this.state = {
      activeTab: this.props.children[0].props.label
    };
  }

  onClickItem = tab => {
    this.setState({ activeTab: tab });
  };

  render() {
    const {
      onClickItem,
      props: { children },
      state: { activeTab }
    } = this;

    return (
      <div className="tabs">
        <ul className="menu-tab-list">
          {children.map(child => {
            const { label } = child.props;
            return (
              <MenuTab
                activeTab={activeTab}
                key={label}
                label={label}
                onClick={onClickItem}
              />
            );
          })}
        </ul>
        <div className="menu-tab-content">
          {children.map(child => {
            if (child.props.label !== activeTab) return undefined;
            return child.props.children;
          })}
        </div>
      </div>
    );
  }
}

export default MenuTabs;
