import React, { Component } from "react";
import classnames from "classnames";
import "./footer-mobile.styles.scss";

class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      prevScrollPos: window.pageYOffset,
      visible: true
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }
  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = () => {
    const { prevScrollPos } = this.state;
    const curScrollPos = window.pageYOffset;
    // console.log(curScrollPos);///
    const visible = prevScrollPos < curScrollPos;
    this.setState({
      prevScrollPos: curScrollPos,
      visible
    });
  };
  render() {
    return (
      <footer
        className={classnames("footer", {
          "footer--hidden": !this.state.visible
        })}
      >
        <a className="location-text-phone" href="tel:608-422-5477">
          <div className="footer-container">
            <span>Call (608)422-5477</span>
          </div>
        </a>
      </footer>
    );
  }
}
export default Footer;
