import React, { Component } from "react";
import HeaderLinks from "../header-links/header-links.component";
import "./hamburger-menu.styles.scss";
class HamburgerMenu extends Component {
  constructor(props) {
    super(props);
    // Hamburger menu visibility with input checkbox
    this.state = {
      checked: false
    };
    this.handleHideMenu = this.handleHideMenu.bind(this);
  }
  handleToggleMenu = () => {
    // console.log("Clicked: toggler"); ///
    // Toggle visibility(checked)
    this.setState(prevState => ({ checked: !prevState.checked }));
  };
  // Passed down to header-mobile to hide menu(hamburger) on clicking NavLink
  handleHideMenu = () => {
    this.setState({ checked: false });
  };

  render() {
    // console.log(this.state); //
    // console.log(this.props); ///

    return (
      <div
        className={
          "hamburger-menu-wrap " +
          (this.props.scrolled ? "hamburger-menu-wrap--reposition" : "")
        }
      >
        {/* Transform menu down when scrolled  */}
        <input
          type="checkbox"
          className="toggler"
          checked={this.state.checked}
          onChange={this.handleToggleMenu}
        />
        <div className="hamburger">
          <div className="hamburger-lines">
            {/* Lines are drawn here with sass */}
          </div>
        </div>
        <div className="hamburger-menu">
          <div>
            <div>
              {/* TODO: fix prop drilling */}
              <HeaderLinks
                handleShowModal={this.props.handleShowModal}
                handleHideMenu={this.handleHideMenu}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default HamburgerMenu;
