import React from "react";
// Used to animate
import { Transition, animated } from "react-spring/renderprops";
import "./announcement.styles.scss";
import noodleIcon from "../../assets/images/noodle-color.png";

class Announcement extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      isActive: true
    };
  }

  hideAnnouncement() {
    this.setState({
      isActive: false
    });
  }
  render() {
    return (
      <div className="announcement-container">
        <Transition
          native
          items={this.state.isActive}
          from={{ transform: "translate3d(0, -40px, 0)", opacity: 0 }}
          enter={{ transform: "translate3d(0, 0px, 0)", opacity: 1 }}
          leave={{ transform: "translate3d(0, -40px, 0)", opacity: 0 }}
          // config={{ delay: 1000, duration: 1000 }}
          config={{ duration: 600 }}
        >
          {show =>
            show &&
            (props => (
              <animated.div style={props} className="custom-transition">
                <div className="announcement">
                  <div className="container">
                    <div></div>
                    <div className="text-edit">
                      {/* TODO: Show it for admin only */}
                      <button className="btn btn-edit">Edit</button>
                      <span>
                        <img
                          className="noodle-icon"
                          src={noodleIcon}
                          alt="noodle-icon"
                        />
                        {/* TODO: Add dynamic message by admin */}
                        Ramen Kid will be closing during the holiday season!
                      </span>
                    </div>
                    <button
                      className="btn btn-close"
                      onClick={() => this.hideAnnouncement()}
                    >
                      CLOSE
                    </button>
                  </div>
                </div>
              </animated.div>
            ))
          }
        </Transition>
      </div>
    );
  }
}

export default Announcement;
